html {
  height: 100%;
}

body {
  width: 100%;
  min-width: $desktop-min-width;
  background-color: $white;
  margin: 0 auto;
  text-align: center;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-style: normal;
  color: $black-text;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  position: relative;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%);
}

.up-button {
  position: fixed;
  right: 26px;
  bottom: 30px;
  text-align: center;
  cursor: pointer;

  svg {
    margin: 0 auto 12px;
  }

  &:hover {
    p {
      color: $hover-text;
    }

    &::before {
      box-shadow: 0px 10px 16px rgba(0, 0, 120, 0.1);
      background-image: url("../img/arrow_up-hover-icon.svg");
    }
  }

  p {
    margin: 0;
    font-size: 14px;
    line-height: 17px;
    font-weight: bold;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $light-gray;
  }

  &::before {
    display: block;
    content: "";
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin: 0 auto 12px;
    background-image: url("../img/arrow_up-icon.svg");
  }
}

.networks {
  position: fixed;
  right: 6px;
  top: 180px;
}

.networks__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.networks__item {
  width: 36px;
  margin: 0 0 5px;
}

.networks__item-link {
  svg {
    width: 36px;
    height: 36px;
    fill: $blue;
  }

  svg:hover {
    fill: $hover-text;
  }
}

.button {
  text-align: center;
  display: block;
  box-sizing: border-box;
  padding: 20px 20px;
  width: fit-content;
  font-size: 14px;
  line-height: 17px;
  font-weight: bold;
  color: $white;
  background-color: $blue;
  border-radius: 8px;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    background-color: $hover-text;
    box-shadow: 0 10px 16px rgba(45, 128, 252, 0.2);
  }
}

@media (max-width: $tablet-max-width) {
  body {
    min-width: $tablet-min-width;
  }
}

@media (max-width: $mobile-max-width) {
  body {
    min-width: $mobile-min-width;
  }

  .up-button {
    display: none;
  }

  .networks {
    display: none;
  }
}
